body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background: #fff;
  color: #222;
  transition: all 0.3s ease;
}

.app.dark {
  background-color: #111;
  color: #eee;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: linear-gradient(to right, #6a11cb, #2575fc);
  color: white;
}

.header nav a {
  margin: 0 10px;
  color: white;
  text-decoration: none;
}

.resume-btn {
  background-color: white;
  color: #2575fc;
  padding: 6px 12px;
  border-radius: 6px;
  text-decoration: none;
  margin-left: 10px;
}

.toggle-btn {
  margin-left: 20px;
  padding: 4px 8px;
  font-size: 18px;
  background: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

section {
  padding: 40px 20px;
}

.project {
  border: 1px solid #ccc;
  padding: 15px;
  margin: 15px 0;
  border-radius: 8px;
}

.socials a {
  margin-right: 15px;
  text-decoration: none;
  color: #2575fc;
}

